import { useState } from 'react';
import Hotkeys from 'react-hot-keys';

const PEALIM_BASE = 'https://www.pealim.com/search/?q=';

function openInNewTab(url) {
  window.open(url, '_blank').focus();
}

export default function Words() {
  const [isAPressed, setAState] = useState(false);

  const onKeyUp = (keyName) => {
    if (keyName === 'a') {
      setAState(false);
    }
  };
  const onKeyDown = (keyName) => {
    if (keyName === 'a') {
      setAState(true);
    }
  };

  const onClick = (event) => {
    console.log(event.currentTarget.textContent);
    if (isAPressed) {
      console.log('isAPressed');
      openInNewTab(PEALIM_BASE + event.currentTarget.textContent);
    }
  };

  return (
    <Hotkeys keyName="a" onKeyDown={onKeyDown} onKeyUp={onKeyUp}>
      <h2 className="pointer" onClick={onClick}>
        
      </h2>
    </Hotkeys>
  );
}
